@import url(https://fonts.googleapis.com/css?family=Karla|Miniver&display=swap);
@import url(https://fonts.googleapis.com/css?family=Staatliches&display=swap);
.Sidebar{background:#deb887;display:flex;flex-direction:column;padding:100px 50px;text-align:center}.Sidebar ul{list-style:none;margin:0;padding:0}.Sidebar ul li{padding:20px 0}.Sidebar ul li a{text-decoration:none;color:#000}.Sidebar ul li a:hover{color:#000}.Sidebar .logo{font-family:"Miniver",cursive;font-size:2rem}
.ProjectThumbnail{margin:0 0;position:relative;cursor:pointer}.ProjectThumbnail .bg-image{background-size:contain !important;background-repeat:no-repeat !important;background-position:center !important;width:inherit;height:inherit;filter:blur(0px);-webkit-filter:blur(0px)}.ProjectThumbnail .bg-text{background-color:rgba(0,0,0,.4);color:#fff;font-weight:bold;border:3px solid #f1f1f1;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);z-index:2;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:20px 50px;text-align:center}.mytooltip{position:fixed;background-color:rgba(0,0,0,.8);color:#fff;padding:5px;border-radius:5px;pointer-events:none;z-index:300;opacity:.7}
.projects-container{display:flex;flex-wrap:wrap;justify-content:space-evenly;grid-gap:40px;gap:40px;margin:4em}
.Home{padding:50px;font-size:1.3rem}.Home h2{text-align:center}.Home p{padding-top:100px}
body{margin:0;font-family:"Karla",sans-serif;overflow:hidden}body .Container{margin:0}body .Container h1,body .Container h2,body .Container h3,body .Container h4,body .Container h5,body .Container h6{font-family:"Staatliches",cursive;font-size:3.5rem;margin:0}body .Container .content{background:#ffefd9;padding:20px;height:100vh;overflow-y:auto}
