@import url("https://fonts.googleapis.com/css?family=Karla|Miniver&display=swap");
@import url("https://fonts.googleapis.com/css?family=Staatliches&display=swap");

body {
  margin: 0;
  font-family: "Karla", sans-serif;
  overflow: hidden;

  .Container {
    margin: 0;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-family: "Staatliches", cursive;
      font-size: 3.5rem;
      margin: 0;
    }

    .content {
      background: rgb(255, 239, 217);
      padding: 20px;
      height: 100vh;
      overflow-y: auto;
    }
  }
}
